$fsize: 16px;
$header: 25px;
.Lab {
    width: 100%;
    height: 100%;
    background-color: white;
    border-radius: 4px;
    box-shadow: var(--shadow);
    overflow: hidden;
    position: relative;
    .sections {
        .section {
            width: 50%;
            height: 100%;
            position: relative;
            background-color: #343842;
            padding-top: $header;
            overflow: hidden;
            .header {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: $header;
                background-color: #2d303a;
                color: #656565;
                font-size: 0.8em;
                z-index: 111;
            }
            &:nth-child(1) {
                border-right-style: solid;
                border-width: 1px;
                border-color: #656565;
                .scss {
                    position: relative;
                    overflow: auto;
                }
            }
            &:nth-child(2) {
                border-left-style: solid;
                border-width: 1px;
                border-color: #656565;
                .css{
                    position: relative;
                    ._mirosass_wrap {
                        height: 100% !important;
                        overflow-y: auto !important;
                    }
                    &:before {
                        content: "save";
                        color: white;
                        position: absolute;
                        bottom: 15px;
                        right: 15px;
                        width: 80px;
                        height: 37px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        border-radius: 4px;
                        background-color: #97c279;
                        letter-spacing: 1px;
                        opacity: 0.9;
                        cursor: pointer;
                        display: none;
                        
                    }
                    &.show_save:before {
                        display: flex;
                    }
                }
            }
        }
        @media screen and (max-width: 890px){
            &{
                flex-direction: column;
                .section {
                    width: 100%;
                }
            }
            
        }
    }
    .codeflask__textarea {
        background-color: #343842;
        caret-color: #56b5c2 !important;
    }
    .codeflask {
        font-family: unset; 
        background-color: #343842;
        color: #aab1bf !important;
        font-size: $fsize;
        .token.punctuation { color: #aab1bf !important; }
        .token.keyword { color: #c578dd !important; }
        .token.operator { color: #56b5c2 !important; }
        .token.string { color: #97c279 !important; }
        .token.comment { color: #9badb7; }
        .token.function { color: #61afef !important; }
        .token.boolean { color: #d19965 !important; }
        .token.number { color: #d19965 !important; }
        .token.selector { color: #d19965 !important; }
        .token.property { color: #56b5c2 !important; }
        .token.tag { color: #df6b75 !important; }
        .token.attr-name { color: #d19965 !important; }
        .token.attr-value { color: #97c279 !important; }
        .codeflask--has-line-numbers:before {
            background: #2d303a !important;
            z-index: 3 !important;
        }
        * {
            font-family: unset !important;
            font-size: $fsize;
        }
        textarea {
            color: transparent;
            font-size: $fsize;
        }
        pre {
            width: auto !important;
        }
        .codeflask__lines__line {
            background: #2d303a;
        }
    }
    .codeflask__lines {
        background-color: #2d303a;
        z-index: 4 !important;
        padding-left: 0 !important;
    }
    .codeflask.codeflask--has-line-numbers:before {
        background: #2d303a !important;
    }
}