.card {
    background-color: white;
    width: 100%;
    box-shadow: var(--shadow);
    border-radius: 4px;
    margin-bottom: 30px;
    padding: 20px;
    .title {
        color: #26235b;
    }
    p {
        color: #555475;
        font-size: 1.1em;
        letter-spacing: 0.008em;
        line-height: 1.5em;
    }
    .links {
        a {
            margin-left: 10px;
            color: #9a6be2;
            width: 100%;
            word-wrap: break-word;
        }
        p {
            display: flex;
            align-items: flex-start;
            justify-content: flex-start;
            @media screen and (max-width: 600px) {
                &{
                    flex-direction: column;
                }
            }
        }
    }
}