$header: 100px;
.App {
    width: 100%;
    height: 100%;
    background-color: var(--c0);
    padding: 40px;
    overflow-y: auto;
    display: block;
    @media screen and (max-width: 800px) {
        & {
            padding: 25px;
        }
    }
    @media screen and (min-width: 1200px) {
        & {
            display: flex;
            flex-direction: row !important;
            .card {
                width: 45%;
                margin:  0;
                margin-right: 30px;
                height: 100%;
                overflow-y: auto;
                p {
                    flex-direction: column !important;
                }
            }
        }
    }
}
